$primary-color: #6c63ff;
$bg-accent: #fbfaff;

html,
body {
  font-family: 'Muli', sans-serif;
  margin: 0;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

/*
    Navigation  
*/

nav {
  height: 100px;
  display: flex;
  padding: 0px 40px;
  align-items: center;
  .nav__link {
    text-decoration: unset;
    color: $primary-color;
    transition: 200ms;
    &:hover {
      color: black;
    }
  }
  .nav__link-title {
    font-size: 40px;
    font-weight: bolder;
    margin-right: auto;
    color: black;
  }
}

/*
    Main wrapper
*/

main {
  min-width: 300px;
  max-width: 800px;
  width: 80vw;
  min-height: calc(100vh - 100px - 100px);
  margin: 60px auto 0 auto;
  border-radius: 20px 20px 0px 0px;
  padding: 20px;
  overflow: hidden;
}
